.Chatlistitem{
    display: flex;
    cursor: pointer;
    align-items: center;
    height: 70px;
}

.Chatlistitem:hover{
    background-color: #f5f5f5;
}

.Chatlistitem.active{
    background-color: #ebebeb;
}

.chatlistitem--avatar{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 15px;
}

.chatlistitem--lines{
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 1px solid #EEE;
    padding-right: 15px;
    margin-left: 15px;

    flex-wrap: wrap;
    min-width: 0;

}

.chatlistitem--line{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.chatlistitem--name{
    font-size: 17px;
    color: black;
}

.chatlistitem--date{
    font-size: 12px;
    color: #999;
}

.chatlistitem--lastmsg{
    font-size: 14px;
    color: #999;
    display: flex;
    width: 100%;
}

.chatlistitem--lastmsg p{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 0;
}